import { FC } from 'react';
import classNames from 'classnames';

//Styles
import './PatternHighlighter.scss';

export interface HighlighterProps {
  /* Pattern to match */
  highlighter: string;
  children?: JSX.Element | string;
  className?: string;
}

const PatternHighlighter: FC<HighlighterProps> = ({
  className,
  highlighter = '',
  children = '',
}) => {
  const shouldBeHighlighted = new RegExp(`(${highlighter})`, 'gi');

  const text =
    typeof children == 'string'
      ? children
      : children.props.children
      ? children.props.children.toString()
      : '';

  return highlighter.trim() ? (
    <>
      {text.split(shouldBeHighlighted).map((value: string) => (
        <span
          key={value}
          className={classNames(
            { 'sb-pattern-highlighter': shouldBeHighlighted.test(value) },
            className,
          )}
        >
          {value}
        </span>
      ))}
    </>
  ) : (
    <span>{children}</span>
  );
};
export default PatternHighlighter;
